import Modal from 'bootstrap/js/dist/modal';

export default {
    methods: {
        showModal() {
            // show()modal
            this.modal.show();
        },
        
        hideModal() {
            // hide()modal
            this.modal.hide();
        }
    },

    mounted() {
        // Modalmodal
        this.modal = new Modal(this.$refs.modal);       
    }
};